import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import authService from '../services/authService';
import './ProfileDropdown.css';

const ProfileDropdown = ({ user }) => {
    const [isOpen, setIsOpen] = useState(false);
    const wrapperRef = useRef(null);
    const navigate = useNavigate();

    useEffect(() => {
        const handleClickOutside = (e) => {
            if (wrapperRef.current && !wrapperRef.current.contains(e.target)) {
                setIsOpen(false);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, []);

    const initial = user?.name ? user.name.charAt(0) : '?';

    return (
        <div className="profile-dropdown-wrapper" ref={wrapperRef}>
            <button
                className="profile-avatar-btn"
                onClick={() => setIsOpen(prev => !prev)}
                aria-expanded={isOpen}
                aria-haspopup="true"
            >
                {initial}
            </button>

            {isOpen && (
                <div className="profile-dropdown-menu">
                    <div className="profile-dropdown-header">
                        <div className="profile-dropdown-name">{user?.name}</div>
                        <div className="profile-dropdown-email">{user?.email}</div>
                        {user?.role && (
                            <span className="profile-dropdown-role">{user.role}</span>
                        )}
                    </div>
                    <div className="profile-dropdown-items">
                        <button
                            className="profile-dropdown-item"
                            onClick={() => {
                                setIsOpen(false);
                                navigate('/profile');
                            }}
                        >
                            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2" />
                                <circle cx="12" cy="7" r="4" />
                            </svg>
                            My Profile
                        </button>
                        <div className="profile-dropdown-divider" />
                        <button
                            className="profile-dropdown-item danger"
                            onClick={() => authService.logout()}
                        >
                            <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="#EA4335" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4" />
                                <polyline points="16 17 21 12 16 7" />
                                <line x1="21" y1="12" x2="9" y2="12" />
                            </svg>
                            Logout
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default ProfileDropdown;
