/**
 * Google Sheets  Client-side preview parser
 * Fetches column headers and row data directly from a public Google Sheet
 * using the Google Visualization / gviz endpoint (no backend required).
 * The sheet URL is also passed as-is to the backend during generation.
 */

const sheetService = {
    /**
     * Fetch column headers and rows from a public Google Sheets URL.
     * @param {string} googleSheetUrl - Full Google Sheets URL
     * @returns {Promise<{ columns: string[], data: object[] }>}
     */
    parse: async (googleSheetUrl) => {
        // Extract spreadsheet ID from the URL
        const match = googleSheetUrl.match(/\/spreadsheets\/d\/([a-zA-Z0-9-_]+)/);
        if (!match) throw new Error('Invalid Google Sheets URL. Make sure to paste the full URL.');

        const sheetId = match[1];

        // Use Google Visualization API  works for sheets shared as "Anyone with the link"
        const gvizUrl = `https://docs.google.com/spreadsheets/d/${sheetId}/gviz/tq?tqx=out:json`;
        const response = await fetch(gvizUrl);

        if (!response.ok) {
            throw new Error(`Could not access the sheet (HTTP ${response.status}). Make sure the sheet is shared publicly.`);
        }

        const text = await response.text();

        // Strip JSONP wrapper: /*O_o*/\ngoogle.visualization.Query.setResponse({...});
        const jsonStr = text.match(/google\.visualization\.Query\.setResponse\(([\s\S]+)\);?\s*$/)?.[1];
        if (!jsonStr) throw new Error('Failed to parse Google Sheets response.');

        const json = JSON.parse(jsonStr);
        const table = json.table;

        if (!table || !table.cols) throw new Error('Sheet appears to be empty or inaccessible.');

        // Extract column names from first row headers
        const columns = table.cols
            .map(col => col.label || col.id)
            .filter(Boolean);

        // Extract row data as array of objects keyed by column name
        const data = (table.rows || []).map(row => {
            const obj = {};
            (row.c || []).forEach((cell, i) => {
                if (columns[i] !== undefined) {
                    obj[columns[i]] = cell?.v ?? '';
                }
            });
            return obj;
        });

        return { columns, data };
    },
};

export default sheetService;
