import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import useAuth from '../hooks/useAuth';
import './HomePage.css';

function HomePage() {
    const { user, loading, isLoggedIn } = useAuth();
    const navigate = useNavigate();

    if (loading) {
        return <div className="home-loading">Loading...</div>;
    }

    return (
        <div className="home-page">
            {/* Hero */}
            <div className="home-hero">
                {!isLoggedIn ? (
                    <>
                        <h1 className="home-hero-title">
                            <span>GDGoC Certificate</span>
                        </h1>
                        <p className="home-hero-subtitle">Dynamic SVG Certificate &amp; Asset Generator</p>
                        <p className="home-hero-desc">
                            Automate bulk personalized certificate generation for your events.
                        </p>
                        <div className="home-hero-actions">
                            <Link to="/login" className="home-btn-outline">Sign In</Link>
                            <Link to="/register" className="home-btn-primary">Get Started </Link>
                        </div>
                        <div className="home-features">
                            <div className="home-feature-card">
                                <span className="home-feature-icon"></span>
                                <div className="home-feature-title">SVG to PDF</div>
                                <div className="home-feature-desc">Convert SVG templates to high-quality PDFs</div>
                            </div>
                            <div className="home-feature-card">
                                <span className="home-feature-icon"></span>
                                <div className="home-feature-title">Google Sheets</div>
                                <div className="home-feature-desc">Read participant data directly from Google Sheets</div>
                            </div>
                            <div className="home-feature-card">
                                <span className="home-feature-icon"></span>
                                <div className="home-feature-title">Bulk Email</div>
                                <div className="home-feature-desc">Send certificates automatically via Gmail</div>
                            </div>
                        </div>
                    </>
                ) : (
                    <>
                        <h1 className="home-welcome-title">Welcome back, {user.name}!</h1>
                        <p className="home-welcome-sub">Ready to generate certificates?</p>
                        <div className="home-action-cards">
                            <div className="home-action-card" onClick={() => navigate('/generator')}>
                                <span className="home-action-icon"></span>
                                <div className="home-action-title">New Generation</div>
                                <div className="home-action-desc">Generate certificates from a template</div>
                            </div>
                            <div className="home-action-card" onClick={() => navigate('/templates')}>
                                <span className="home-action-icon"></span>
                                <div className="home-action-title">My Templates</div>
                                <div className="home-action-desc">View and manage your saved templates</div>
                            </div>
                            <div className="home-action-card" onClick={() => navigate('/create')}>
                                <span className="home-action-icon"></span>
                                <div className="home-action-title">Create Template</div>
                                <div className="home-action-desc">Design a new certificate template</div>
                            </div>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
}

export default HomePage;
