import React from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import useAuth from '../hooks/useAuth';
import ProfileDropdown from './ProfileDropdown';
import './Navbar.css';

function Navbar() {
    const location = useLocation();
    const navigate = useNavigate();
    const { user, loading, isLoggedIn } = useAuth();

    return (
        <nav className="navbar">
            <div className="navbar-left">
                <Link to="/" className="navbar-logo">
                    <img src="/assets/bugkathon_logo.svg" alt="Bugkathon" />
                    <span className="navbar-logo-text">GDGoC Certificate</span>
                </Link>
                {isLoggedIn && (
                    <>
                        <Link
                            to="/generator"
                            className={`navbar-link${location.pathname === '/generator' ? ' active' : ''}`}
                        >
                            Generator
                        </Link>
                        <Link
                            to="/create"
                            className={`navbar-link${location.pathname === '/create' ? ' active' : ''}`}
                        >
                            Create New
                        </Link>
                        <Link
                            to="/templates"
                            className={`navbar-link${location.pathname === '/templates' ? ' active' : ''}`}
                        >
                            My Templates
                        </Link>
                    </>
                )}
            </div>
            <div className="navbar-right">
                {!loading && !isLoggedIn && (
                    <>
                        <button className="navbar-btn-signin" onClick={() => navigate('/login')}>Sign In</button>
                        <button className="navbar-btn-register" onClick={() => navigate('/register')}>Register</button>
                    </>
                )}
                {!loading && isLoggedIn && (
                    <ProfileDropdown user={user} />
                )}
            </div>
        </nav>
    );
}

export default Navbar;
